<i18n>
{
  "en": {
    "MyWallet": "Exchange coupon",
    "tip": "Please enter the exchange code",
    "coin": "Exchange"
  },
  "zh": {
    "MyWallet": "兑换惠券",
    "tip": "请输入兑换码",
    "coin": "兑换"
  }
}
</i18n>

<template>
  <div class="wallet">
    <head-top :title='$t("MyWallet")' :go-back="true" @goBackHander="goBackHander">
    </head-top>
    <div class="container">
      <div class="column">
        <input type="text" :placeholder="$t('tip')">
        <span>{{$t('coin')}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
export default {
  name: 'wallet',
  data () {
    return {
      deposit: 0, // 押金
      amount: 0,
      depositStandard: 0 // 应收押金
    }
  },

  components: {
    headTop
  },

  mounted () {
    this.dataInteractionResponse(res => {
      // console.log(res)
      switch (res.msgType) {
        // 查询押金及余额返回
        case '0x5242':
          if (res.result && res.data) {
            this.deposit = res.data.deposit
            this.amount = res.data.amount
          }
          break
        case '0x5246':
          if (res.result && res.data) {
            if (res.data.isDeposit === 1) {
              this.$router.push({
                name: 'deposit',
                params: {
                  deposit: this.deposit,
                  depositStandard: res.data.depFee
                }
              })
            } else {
              this.setGlobalToast({
                bol: true,
                zh: '不需要交押金喔~',
                en: 'No deposit required!'
              })
            }
          }
          break
      }
    })
    // 查询押金及余额请求
    this.dataInteractionRequest({
      msgType: '0x5241'
    })
  },

  methods: {
    ...mapActions([
      'setGlobalToast'
    ]),
    goBackHander () {
      // console.log(123)
    },
    itemHandle (num) {
      switch (num) {
        case 1:
          this.$router.push({'path': '/transaction'})
          break
        case 2:
          this.$router.push({'path': '/recharge'})
          break
        case 3:
          if (this.deposit === 0) {
            // 查询收费标准请求
            this.dataInteractionRequest({
              msgType: '0x5245'
            })
            if (this.ISDEBUG) {
              let str = '{"msgType":"0x5246","result":true,"data":{"id":"2","depFee":0,"isDeposit":2,"remark":"免押金"}}'
              window.dataInteractionResponse(str)
            }
          } else {
            // 进入退押金界面
            // this.$router.push({
            //   name: 'deposit',
            //   params: {
            //     deposit: this.deposit,
            //     depositStandard: this.deposit
            //   }
            // })
          }
          break
        case 4:
          // if (!this.amount) {
          //   return false
          // }
          // this.$router.push({'path': '/withdraw'})
          break
      }
    },
    withdraw () {
      if (!this.amount) {
        return false
      }
      this.$router.push({'path': '/withdraw'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.wallet{
  .fs;
  background: #FFF;
  .transaction{
    background: @statusBarColor;
    .wh(100px,100%);
    float: right;
    margin-right: 10%;
  }
  .container{
    .column{
      .wh(100%, 100%);
      margin: 50px 50px 0 50px;
      display: flex;
      input{
        .wh(60%, 80px);
        border-color: @themeColor;
        padding-left: 10px;
        margin-right: 30px;
        border: 1px solid @themeColor;
      }
      :placeholder{
        color: #c1c1c1;
      }
      span{
        .wh-l(20%, 80px);
        font-size: 28px;
        color: white;
        text-align: center;
        background: @themeColor;
        border-radius: 5px;
      }
    }
  }
}
</style>



